.nav-button{
    border-radius:20px;
    width:130px;
    height:35px;
    display: flex;
    font-size: 17px;
    justify-content: center;
    align-items : center;
    background-color:rgba(0,0,0,0);
    color:var(--main-color1);
    border:none;
    cursor: pointer;
    font-family: SCDLight;
}
.nav-button:hover{
    font-weight: 600;
}
.nav-left-text{
    color:black;
    margin-left:2vw;
    cursor: pointer;
    padding:3px 0px;
    border-radius: 5px;
    text-decoration: none;
}
.nav-left-text:hover{
    color:black;
    background-color: #6a63f75b;
}


.nav-on-v2{
    display: flex;
    align-items :center;
    width:100%;
    cursor:pointer;
    justify-content : center;
    margin-right:5vw;
}

.nav-bar-container{
    position: fixed;
    top: 0;
    z-index:3;
    height:60px;
    width: 100%;
    display: flex;
    justify-content: start;
    align-items : center;
    flex-direction: row;
}

.nav-bar-container-top{
    position: fixed;
    top: 0;
    z-index:3;
    height:60px;
    width: 100%;
    display: flex;
    justify-content: start;
    align-items : center;
    flex-direction:row;
    box-sizing: border-box;
    padding:1% 10%;
    background-color: rgba(255,255,255,0.9);
}

.nav-bar-title{
    font-size:36px;
    font-weight: 700;
    text-align:start;
    text-decoration: none;
    color:var(--main-color1);
    width:100%;
}

.nav-on{
    display: flex;
    justify-content :end;
    align-items : center;
    width:100%;
}

.footer{
  background-color:rgb(37, 37, 37);
  display: flex;
  justify-content : center;
  align-items : center;
  color:rgb(131, 131, 131);
  font-weight:400;
  flex-direction: row;
  width:100%;
  padding:50px 10px;
  box-sizing: border-box;
}

.footer-item{
  margin-top:7px;
  width:100%;
  display: flex;
  justify-content : start;
  align-items : center;
    box-sizing: border-box;
    flex-direction:column;
}


.footer-more{
    width:100%;
    padding-left:20%;
    height:60%;
    text-align: left;
    display: flex;
    flex-direction: column;
    justify-content :end;
    box-sizing: border-box;
  }

.footer-text-click{
  color:rgb(131, 131, 131);
    text-decoration: none;
}

.footer-text-click:hover{
    color:black;
}

@media screen and (max-width:800px){
    .nav-button{
        font-size: 15px;
    }
    .nav-bar-title{
        font-size:26px;
    }
    .footer-item{
      justify-content : center;
    }
    .footer-more{
        padding-left:5%;
    }
}